import { motion, useMotionValue, useTransform } from "framer-motion";
import Image from "next/image";
import { Fragment, useEffect, useRef, useState } from "react";

export default function AnimatedRobo() {
  const containerRef = useRef(null);

  const [currentImage, setCurrentImage] = useState("/images/gr.jpg");

  const mouseX = useMotionValue(0);
  const mouseY = useMotionValue(0);

  useEffect(() => {
    const handleMouseMove = (e) => {
      const { left, top, width, height } =
        containerRef.current.getBoundingClientRect();
      const x = e.clientX - left - width / 2;
      const y = e.clientY - top - height / 2;

      const rect = containerRef.current.getBoundingClientRect();
      const centerX = rect.left + rect.width / 2;

      if (e.clientX < centerX - 50) {
        setCurrentImage("/images/4.png");
      } else if (e.clientX > centerX + 50) {
        setCurrentImage("/images/5.png");
      } else {
        setCurrentImage("/images/2.png");
      }

      mouseX.set(x);
      mouseY.set(y);
    };

    const container = containerRef.current;
    container.addEventListener("mousemove", handleMouseMove);

    return () => container.removeEventListener("mousemove", handleMouseMove);
  }, []);

  const rotateX = useTransform(mouseY, [-100, 100], [15, -15]);
  const rotateY = useTransform(mouseX, [-100, 100], [-15, 15]);

  console.log(rotateX);
  console.log(rotateY);

  return (
    <Fragment>
      <div
        className="container relative flex flex-col items-center justify-center w-full"
        ref={containerRef}
      >
        <motion.img
          src={currentImage}
          className="w-[300px] h-auto absolute object-cover top-[30] right-[530]"
          style={{
            rotateX,
            rotateY,
            transformPerspective: 800,
          }}
        />

        <Image src={"/images/ij.jpg"} width={400} height={400} alt="Robot" />
      </div>
    </Fragment>
  );
}
